import { useWeb3React } from "@web3-react/core";
import { useEffect, useState } from "react";
import { encrypt } from "@metamask/eth-sig-util";

export function useWallet() {}

export function usePublicKey() {
  const [pubKey, setPubKey] = useState<string | undefined>();
  const { active, library, account } = useWeb3React();
  const [CB, setCB] = useState(() => () => console.log("default ooops"));

  useEffect(() => {
    if (active && account) {
      const cached = window.localStorage.getItem(account);
      if (cached) return setPubKey(cached);
      library
        .send("eth_getEncryptionPublicKey", [account])
        .then((pub: string) => {
          window.localStorage.setItem(account, pub);
          setPubKey(pub);
        })
        .catch((err: Error) => {
          console.error(err);
        });
    }
  }, [active]);

  return pubKey;
}

